.course__title {
	font-size: 140%;
	margin-bottom: 1rem;
}

.course__description {
	font-size: 90%;
}

.course__date {
	align-content: center;
	background-image: url("../../images/irregular-paint-composed.png");
	background-position: center;
	background-repeat: no-repeat;
	background-size: contain;
	color: black;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	height: 200px;
	margin-top: 2rem;
	padding: 0 1rem;
}

.course__date > p {
	text-align: center;
	font-weight: bold;
}

.course__date__title {
	width: 80%;
	font-family: var(--arvo);
	padding-bottom: .5rem;
}

.course__instructor {
	background-image: url("../../images/irregular-line.png");
	background-position: center;
	background-repeat: no-repeat;
	height: 400px;
	padding: 1rem;
}

.course__instructor__thumbnail {
	border-radius: 100%;
	height: 100px;
	object-fit: cover;
	width: 100px;
}

.course__instructor__name {
	font-family: var(--homemade-apple);
}

.course__instructor__team {
	font-size: 70%;
	font-weight: bolder;
	position: relative;
	top: -10px;
}

.course__instructor__descripcion {
	font-size: 90%;
}

.course__buttons {
	text-align: center;
	padding-bottom: 3rem;
}

@media (min-width: 500px) {
	.course__date__title {
		width: 30%;
	}
}

/* Desktop view */
@media (min-width: 768px) {
	.course__wrapper {
		display: flex;
	}

	.course__presentation {
		width: 60%;
	}

	.course__date__title {
		width: 30%;
	}

	.course__date__subtitle {
		width: 100%;
	}

	.course__wrapper > aside {
		width: 220px;
		height: 100vh;
		background-image: url("../../images/irregular-line-variation.png");
		background-position: center;
		background-repeat: no-repeat;
		background-size: cover;
		text-align: center;
		padding-top: 4rem;
	}

	.course__instructor__descripcion {
		width: 60%;
		margin: 0 auto;
	}

	.course__buttons {
		padding-top: 4rem;
	}

	.button--shape {
		width: 50%;
		font-size: 120%;
	}

	.small {
		padding-top: 1rem;
	}

	.small > span {
		font-size: 80%;
	}

	.button--shape:hover {
		font-size: 150%;
	}

	.container {
		margin: 0 auto;
	}
}
